import React from "react";
import Profile from "./Profile";
import isAuthHoc from "../HOCS/isAuthHoc";
import { connect } from "react-redux";
import { useParams } from "react-router-dom";
import { getUserProfile, getUserStatus, updateStatus, savePhoto, updateProfile } from "../../redux/profile-reducer";
import {withErrorBoundary} from 'react-error-boundary'
import { ErrorFallback } from "../commons/ErrorFallBack";
class ProfileContainer extends React.Component {
  updateUserInfo(){
    let userId = this.props.nowPath;
    if (userId === "") {
      userId = this.props.autorisedUserId;
    }
    this.props.getProfile(userId);
    this.props.getUserStatus(userId);
  }
  componentDidMount() {
    this.updateUserInfo()
    console.log('mount profile')
  }
  componentDidUpdate(prevProps,prevState,snapshot){
    console.log(this.props)
  if(this.props.nowPath!=prevProps.nowPath) this.updateUserInfo()
  }
  render() {
    return (
      <div className="profile-content">
        <Profile
        updateProfile={this.props.updateProfile}
         savePhoto={this.props.savePhoto}
          isOwner ={!this.props.nowPath}
          userStatus={this.props.userStatus}
          isAuth={this.props.isAuth}
          newUserInfo={this.props.newUserInfo}
          updateStatus={this.props.updateStatus}
        />
      </div>
    )
  }
}

const WithAuth = isAuthHoc(ProfileContainer)
const usePp = (props) => {
  const math = useParams(WithAuth);
  console.log(math)
  return (
    <WithAuth
      updateProfile={props.updateProfile}
      savePhoto={props.savePhoto}
      autorisedUserId = {props.autorisedUserId}
      nowPath={math["*"]}
      userStatus={props.userStatus}
      getProfile={props.getUserProfile}
      getUserStatus={props.getUserStatus}
      newUserInfo={props.newUserInfo}
      updateStatus={props.updateStatus}
    />
  );
};
const mapStateToProps = (state) => ({
  newUserInfo: state.profile.userInfo,
  userStatus: state.profile.status,
  autorisedUserId : state.auth.userId,
})
const ProfileApiContainer = connect(mapStateToProps, { getUserProfile, getUserStatus, updateStatus, savePhoto, updateProfile })(usePp);

export default withErrorBoundary(ProfileApiContainer,{
  FallbackComponent: ErrorFallback
});

