import React, {useState} from "react";
import { PostSenderContainer } from "./PostSender";
import Post from "./Post/Post";
import("./MyPosts.css");
export default function MyPosts(props) {
 
  const preMapPostInfo = props.posts;
  const post = preMapPostInfo.map((p) => (
    <Post
      userName={p.uName}
      userLastName={p.uLName}
      title={p.ttl}
      message={p.mssg}
      key={p.id}
      id={p.id}
    />
  ));  
  const onSubmit = (dataItems)=>{
    props.addPostAC(dataItems.PostsTextArea)
  }
  return (
    <div className="posts-area">
      <PostSenderContainer onSubmit={onSubmit} />
      {post}
    </div>
  );
}
