import React from "react";
import { CustomTextarea } from "../../commons/CustomTextarea";
import { reduxForm, Field } from "redux-form";
import {required, maxLength15} from "../../../utils/validators/validators"
const PostSender = (props) => {
    return (
        <>
           <div className="my-post">My posts</div>
             <div className="add-post">
             <Field name="PostsTextArea" placeholder="post" 
             component={CustomTextarea} validate={[required, maxLength15]} />
             </div>
             <button 
             className="add-post__button"
             onClick={()=>props.handleSubmit()}>Add Post</button>
        </>
         );
} 
export const PostSenderContainer = reduxForm({form: "posts"})(PostSender)
