import React from "react";
import MyPostsContainer from "./MyPosts/MyPostsContainer"
import ProfileInfo from "./ProfileInfo";
import { useState } from "react";
import ProfileForm from "./ProfileForm/ProfileForm";
import("./profile.css");
function Profile(props) {
  const [editMode, setEditMode] = useState(false)
  const saveProfile =(profileData)=>{
console.log(profileData)
props.updateProfile(profileData)
setEditMode(false)
  }
  return (
    <div className="profile-content">
    
   {editMode 
    ? <ProfileForm onSubmit={saveProfile} changeEditMode={()=>setEditMode(false)}/>
    :<> <ProfileInfo savePhoto={props.savePhoto} isOwner={props.isOwner}  updateStatus={props.updateStatus} 
      userInfo = {props.newUserInfo} userStatus = {props.userStatus}/>
      <button onClick={(e)=>{setEditMode(true)}}>EDIT PROFILE</button></>
  }
  
     <MyPostsContainer/>
   </div>
   
    )
}
export default Profile;