import React, {useState, useEffect} from "react";
const StatusWithHocs =(props) =>{
    const [editMode, setEditMode] = useState(false)
    const [status, setStatus] = useState(props.status)
    const activateEditMode = () =>{
      setEditMode(true)
    }
    const deactivateEditMode = () =>{
      setEditMode(false)
      props.updateStatus(status)
    }
    const setChangedStatus = (e) =>{
     setStatus(e.currentTarget.value)
    }
    useEffect(()=>{
      setStatus(props.status)
    },[props.status])
    return (
      <div>
        {!editMode && 
          <div>
            <p onClick={()=>activateEditMode()}><b>Status: </b>{props.status}</p>
          </div>
        }
        {editMode && 
          <div>
            <input value={status} onChange={(e)=>setChangedStatus(e)} onBlur={()=>deactivateEditMode()} autoFocus={true} type="text"/>
          </div>
        }
      </div>
    )
  }

export default StatusWithHocs;