import React from "react";
import { reduxForm } from "redux-form"
import createField from "../../commons/helpers/createField";
import { required, maxLength50 } from "../../../utils/validators/validators";
import { useState } from "react";
import { CustomInput, CustomTextarea } from "../../commons/CustomTextarea";
const ProfileForm =(props)=>{
    const [lookingAJob, setLookingAJob]=useState(false)
    return<>
       Full name:{createField([required, maxLength50], "","fullName",CustomInput)}
       Looking for a job  <div>{createField([required], "","lookingForAJob",CustomInput, {type:"checkbox", onClick:()=>setLookingAJob(!lookingAJob)})}</div>
      {lookingAJob && createField([], "looking for a job description","lookingForAJobDescription",CustomTextarea)}
        AboutMe:{createField([], "","aboutMe",CustomTextarea)}
        <button onClick={props.handleSubmit}>Done</button>
    </>
}
const ProfileReduxForm = reduxForm({form: 'editProfile'})(ProfileForm)
export default ProfileReduxForm