import React from "react";
import p from "./Post.module.css";
export default function Post(props) {
  return (
    <div className={p.posts}>
      <div className={p.post}>
        <div className={p.post__header}>
          <div className="user-image-container width_small">
            
            <div className="user-image"></div>
          </div>
          <div className="user-namimg font_size_small">
            <p className="user-name">{props.userName}</p>
            <p className="user-lastname">{props.userLastName}</p>
          </div>
        </div>
        <div className={p.post__body}>
          <p className={p.post__title}>{props.title}</p>
          <p className={p.post__text}>{props.message}</p>
        </div>
      </div>
    </div>
  );
}
